    Form {
        border: 1px solid lightgray;
        border-radius: 10px;
        padding: 20px;
    }
    
    .contactMain {
        text-align: center;
        position: relative;
        top: 390px;
        max-width: 450px;
        margin: 20px auto;
    }
    
    .contactTitle {
        position: relative;
        bottom: -350px;
        font-weight: 600;
        font-size: 3.9rem;
        padding-left: 39vw;
        text-decoration: none !important;
        color: rgb(8, 8, 8);
        pointer-events: none;
        -webkit-text-size-adjust: 100%;
    }
    
    .emergencyTitle {
        position: relative;
        top: 520px;
        text-align: center;
        font-size: 0.6cm;
        font-weight: bold;
    }
    
    .emergencySentance {
        position: relative;
        top: 510px;
        text-align: center;
    }
    
    .companyTitle {
        position: relative;
        top: 460px;
        right: 15vw;
        text-align: center;
    }
    
    .companyOfService {
        position: relative;
        top: 450px;
        text-align: center;
        list-style: none;
        right: 16.5vw;
    }
    
    .areaTitle {
        position: relative;
        top: 620px;
        left: 58vw;
    }
    
    .areaOfService {
        position: relative;
        top: 610px;
        left: 13vw;
        text-align: center;
        list-style: none;
    }
    
    hr {
        position: relative;
        top: 700px;
        padding: 20px;
    }
    
    .copyRight {
        position: relative;
        top: 650px;
        text-align: center;
        font-size: 100%;
    }
    
    @media screen and (min-width:1600px) {
        .contactTitle {
            font-size: 3.5rem;
            position: relative;
            top: 340px;
            right: -4vw;
        }
        .areaOfService {
            position: relative;
            left: 12vw;
        }
    }
    
    @media screen and (max-width: 1080px) {
        .contactTitle {
            font-size: 2.7rem;
            position: relative;
            top: 370px;
            left: -3vw;
        }
        .areaOfService {
            position: relative;
            left: 15vw;
        }
    }
    
    @media screen and (max-width: 768px) {
        .contactTitle {
            font-size: 2.5rem;
            position: relative;
            top: 370px;
            left: -4vw;
        }
    }
    
    @media screen and (max-width:600px) {
        .contactTitle {
            font-size: 2rem;
        }
        .contactMain {
            width: 400px;
        }
        .areaOfService {
            position: relative;
            left: 20vw;
        }
    }