.ServiCarousel {
    position: relative;
    bottom: 60px;
    left: 24vw;
}

.CarouselTitle {
    position: relative;
    bottom: 130px;
    left: 36vw;
    font-weight: 600;
    font-size: 4.0rem;
}

.social-container {
    position: relative;
    background-color: rgba(233, 232, 232, 0.836);
    bottom: -150px;
    width: 100%;
    height: 100px;
}

.socialMedia {
    position: relative;
    top: 38px;
    right: 3vw;
    color: black;
    margin-left: 25vw;
}

@media screen and (min-width:1600px) {
    .ServiCarousel {
        position: relative;
        left: 25vw;
    }
}

@media screen and (max-width:1200px) {
    .CarouselTitle {
        position: relative;
        left: 32vw;
    }
    .ServiCarousel {
        width: 700px;
        height: 480px;
    }
    .socialMedia {
        position: relative;
        top: 38px;
        right: 5vw;
    }
    .social-container {
        position: relative;
        top: 200px;
    }
}

@media screen and (max-width: 1024px) {
    .ServiCarousel {
        width: 650px;
        height: 320px;
        position: relative;
        left: 12vw;
    }
    .social-container {
        top: 250px;
    }
    .CarouselTitle {
        position: relative;
        left: 30vw;
        font-weight: 600;
        font-size: 3.5rem;
    }
    .socialMedia {
        position: relative;
        left: -5vw;
        margin-left: 25vw;
    }
}

@media screen and (max-width: 768px) {
    .CarouselTitle {
        position: relative;
        left: 23vw;
        font-weight: 600;
        font-size: 2.5rem;
    }
    .ServiCarousel {
        width: 470px;
        height: 320px;
        position: relative;
        left: 12vw;
    }
    .social-container {
        position: relative;
        top: 320px;
        width: 100%;
    }
    .socialMedia {
        margin-left: 23vw;
        font-size: 0.8rem;
    }
}

@media screen and (max-width:500px) {
    .CarouselTitle {
        font-size: 2rem;
        left: 28vw;
    }
    .ServiCarousel {
        width: 370px;
        height: 50px;
    }
    .social-container {
        position: relative;
        left: -2vw;
        width: 150%;
    }
}