.serviceList {
    list-style-type: none;
    line-height: 450%;
    position: relative;
    top: -180px;
    padding-left: 28vw;
    padding-top: 190px;
    padding-bottom: 100px;
    background-color: rgba(233, 232, 232, 0.836);
    font-size: 1.6em;
    font-weight: bold;
}

.serviceTitle {
    position: relative;
    bottom: 35px;
    left: 34vw;
    z-index: 100;
    font-weight: 600;
    font-size: 4.0rem;
}

@media screen and (max-width: 1280px) {
    .serviceTitle {
        font-weight: 600;
        font-size: 3.0rem;
        position: relative;
        left: 37vw;
    }
    .serviceList {
        font-size: 160%;
        position: relative;
        right: 9vw;
        width: 120%;
        height: auto;
    }
}

@media screen and (max-width:1080px) {
    .serviceTitle {
        font-weight: 600;
        font-size: 2.8rem;
    }
    .serviceList {
        position: relative;
        right: 13vw;
        font-size: 165%;
    }
}

@media screen and (max-width:768px) {
    .serviceList {
        position: relative;
        right: 15vw;
        font-size: 150%;
    }
    .serviceTitle {
        font-weight: 600;
        font-size: 2.5rem;
        position: relative;
        left: 38vw;
    }
}

@media screen and (max-width:500px) {
    .serviceTitle {
        font-weight: 600;
        font-size: 2.2rem;
        position: relative;
        left: 30vw;
    }
    .serviceList {
        position: relative;
        bottom: 13px;
        right: 25vw;
        width: 150%;
        font-size: 1.5rem;
    }
}