    .companyLogo {
        background-image: url("../../image/companyLogo.jpg");
        position: relative;
        top: -48px;
        right: 5vw;
        float: left;
        width: auto;
        height: 240px;
    }
    
    .centerLogo {
        background-image: url("../../image/water-repair.jpg");
        position: relative;
        top: -110px;
        width: 100%;
        height: 450px;
    }
    
    .phone {
        position: relative;
        top: -185px;
        font-size: 0.7cm;
        padding-right: 15px;
        white-space: nowrap;
        float: right;
        bottom: 10px;
    }
    
    .app-container {
        width: 350px;
        height: 200px;
        background-color: black;
        color: white;
        transform: translate(42vw, -410px);
    }
    
    .text-app {
        font-weight: bolder;
        text-align: center;
        transform: translate(0vw, 60px);
    }
    
    .app-button {
        transform: translate(48vw, -472px);
        display: flex;
    }
    /* Large to small devices  */
    
    @media screen and (min-width:1600px) {
        .app-button {
            transform: translate(46.5vw, -472px);
            display: flex;
        }
        .companyLogo {
            position: relative;
            right: 13vw;
        }
    }
    
    @media screen and (max-width:1280px) {
        .app-button {
            transform: translate(49vw, -472px);
        }
        .companyLogo {
            position: relative;
            right: 5vw;
        }
    }
    
    @media screen and (max-width:1080px) {
        .app-button {
            transform: translate(38.5vw, -472px);
            display: flex;
        }
        .companyLogo {
            position: relative;
            right: 4vw;
        }
        .app-container {
            transform: translate(30vw, -410px);
        }
    }
    
    @media only screen and (max-width: 768px) {
        .app-button {
            transform: translate(44vw, -472px);
            display: flex;
            -webkit-border-radius: 5px;
            margin: -10px
        }
        .centerLogo {
            width: 92%;
            position: relative;
            left: 6vw;
            height: 400px;
        }
        .companyLogo {
            position: relative;
            left: 20vw;
            top: -64px;
            width: 56%;
            height: auto;
        }
        .phone {
            width: 54%;
            position: relative;
            top: -130px;
            font-size: 140%;
        }
        .app-container {
            transform: translate(30vw, -410px);
            width: 50%;
        }
    }
    
    @media screen and (max-width: 500px) {
        .companyLogo {
            width: 70%;
        }
        .phone {
            position: relative;
            right: 2vw;
        }
        .app-button {
            position: relative;
            right: 5vw;
        }
    }