    .display-3 {
        position: relative;
        top: -940px;
        left: 42vw;
    }
    
    .aboutTitle {
        position: relative;
        bottom: 250px;
        left: 43vw;
        font-weight: 600;
        font-size: 4.0rem;
        padding-bottom: 100px;
    }
    
    .proplumImage {
        background-image: url("../../image/about1.jpg");
    }
    
    div.card {
        border: 0px solid;
        border-radius: 0.1rem;
    }
    
    .card {
        width: 80%;
        position: relative;
        top: -190px;
        left: 10vw;
    }
    
    div.title card-title h5 {
