    .title {
        text-align: center;
    }
    
    .plumbOption {
        text-align: center;
        position: relative;
        right: 6vw;
    }
    
    .plumOptText {
        width: 80%;
        height: auto;
        position: relative;
        left: -1vw;
        font-size: 90%;
    }
    
    .drain {
        background-image: url("../../image/about2.jpeg");
        position: relative;
        left: 1vw;
        width: 70%;
